.mainHeader {

}

.wrapper{
    display: flex;
    height: 9vh;
}



.title {
    margin: auto;
}
.title h1 {

    font-family: 'Cedarville Cursive', cursive;
    font-size: 24px;
    line-height: 39px;
    color: #092a61;
}

.btnWrap{
    display: flex;
    width: 60%;
}

a {
    text-decoration: none;
}

.btn{
    height: 100%;
    width: 173px;
    border: none;
    background-color: inherit;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: SFUIDisplayRegular;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.4px;
    color: #2D2E46;
    opacity: 0.7;
}
.active{
    background-color: rgba(70, 147, 170, 0.19);
    opacity:0.9;
}
.btn:hover{
    border-bottom: 3px solid #092a61;

}

.btnImg{
    margin-right: 10px;
}
.btnLogout {
    height: 23px;
}
